<!-- 顶部信息栏 -->
<template>
  <div>
    <header id="topbar">
      <el-row>
        <el-col :span="4" class="topbar-left">
          <i class="iconfont icon-kaoshi"></i>
          <span class="title" @click="index()">EXAM-SYSTEM</span>
        </el-col>
        <el-col :span="20" class="topbar-right">
          <i class="el-icon-menu" @click="toggle()"></i>
          <div class="user">
            <span>{{ user.userName }}</span>
            <img
              src="@/assets/img/avatar1.jpg"
              class="user-img"
              ref="img"
              @click="showSetting()"
            />
            <transition name="fade">
              <div class="out" ref="out" v-show="login_flag">
                <ul>
                  <li>
                    <a href="javascript:;" @click="modifyPwd()">修改密码</a>
                  </li>
                  <li class="exit" @click="exit()">
                    <a href="javascript:;">退出登录</a>
                  </li>
                </ul>
              </div>
            </transition>
          </div>
        </el-col>
      </el-row>
    </header>
    <el-dialog
      title="修改你的密码"
      :visible.sync="dialogFormVisible"
      width="500px"
      @close="resetForm('form')"
    >
      <el-form :model="form" :rules="rules2" ref="form">
        <el-form-item
          label="身份证号"
          prop="cardId"
          class="pass"
          :label-width="formLabelWidth"
        >
          <el-input
            type="text"
            v-model="form.cardId"
            autocomplete="off"
            placeholder="请输入身份证号"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="新密码"
          prop="pwd"
          class="pass"
          :label-width="formLabelWidth"
        >
          <el-input
            type="password"
            v-model="form.pwd"
            autocomplete="off"
            placeholder="请输入密码"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="确认密码"
          prop="confirmPwd"
          :label-width="formLabelWidth"
        >
          <el-input
            type="password"
            v-model="form.confirmPwd"
            autocomplete="off"
            placeholder="请再次输入密码"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('form')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import store from "@/vuex/store";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.form.pwd) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      dialogFormVisible: false,
      form: {
        cardId: "",
        pwd: "",
        confirmPwd: ""
      },
      formLabelWidth: "80px",
      login_flag: false,
      user: {
        //用户信息
        userName: null,
        userId: null
      },
      rules2: {
        pwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { min: 6, message: "密码不能少于6位字符", trigger: "blur" }
        ],
        cardId: [
          { required: true, message: "身份证号不能为空", trigger: "blur" }
        ],
        confirmPwd: [{ validator: validatePass2, trigger: "blur" }]
      }
    };
  },
  created() {
    this.getUserInfo();
  },
  computed: mapState(["flag", "menu"]),
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let teacherId = this.$cookies.get("tid");
          this.$axios({
            //修改密码
            url: `/api/treset`,
            method: "post",
            data: {
              pwd: this.form.pwd,
              teacherId,
              cardId: this.form.cardId
            }
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                showClose: true,
                message: "修改密码成功，请重新登录",
                type: "success"
              });
              this.$router.push({
                name: "login",
                params: {
                  username: teacherId
                }
              });
            } else if (res.data.code == 111) {
              this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            } else {
              this.$message({
                showClose: true,
                message: "身份信息与当前账号不匹配，请重试",
                type: "error"
              });
            }
          });
        } else {
          return false;
        }
      });
    },
    modifyPwd() {
      this.dialogFormVisible = true;
    },
    //显示、隐藏退出按钮
    showSetting() {
      this.login_flag = !this.login_flag;
    },
    //左侧栏放大缩小
    ...mapMutations(["toggle"]),
    getUserInfo() {
      //获取用户信息
      let userName = this.$cookies.get("tname");
      let userId = this.$cookies.get("tid");
      this.user.userName = userName;
      this.user.userId = userId;
    },
    index() {
      this.$router.push({ path: "/index" });
    },
    exit() {
      let role = this.$cookies.get("role");
      this.$router.push({ path: "/" }); //跳转到登录页面
      this.$cookies.remove("tname"); //清除cookie
      this.$cookies.remove("tid");
      this.$cookies.remove("role");
      if (role == 0) {
        this.menu.pop();
      }
    }
  },
  store
};
</script>

<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
#topbar {
  position: relative;
  z-index: 10;
  background-color: #124280;
  height: 80px;
  line-height: 80px;
  color: #fff;
  box-shadow: 5px 0px 10px rgba(0, 0, 0, 0.5);
}
#topbar .topbar-left {
  height: 80px;
  display: flex;
  justify-content: center;
  background: rgba(0, 0, 0, 0.05);
  overflow: hidden;
}
.topbar-left .icon-kaoshi {
  font-size: 60px;
}
.topbar-left .title {
  font-size: 20px;
  cursor: pointer;
}
.topbar-right {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.topbar-right .user-img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.topbar-right .el-icon-menu {
  font-size: 30px;
  margin-left: 20px;
}
.topbar-right .user {
  position: relative;
  margin-right: 40px;
  display: flex;
}
.topbar-right .user .user-img {
  margin-top: 15px;
  margin-left: 10px;
  cursor: pointer;
}
.user .out {
  font-size: 14px;
  position: absolute;
  top: 80px;
  right: 0px;
  background-color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 12px;
  width: 60px;
}
.user .out ul {
  list-style: none;
}
.user .out ul > li {
  height: 26px;
  line-height: 26px;
}
.out a {
  text-decoration: none;
  color: #000;
}
.out .exit {
  margin-top: 4px;
  padding-top: 4px;
  border-top: 1px solid #ccc;
}
</style>



